        <div class="m-wiki-post-empty" v-if="is_empty">
            <i class="el-icon-s-opportunity"></i>
            <span>æš‚æ— æ”»ç•¥ï¼Œæˆ‘è¦</span>
            <a class="s-link" :href="publish_url(`achievement/${id}`)">å®Œå–„æ”»ç•¥</a>
        </div>
    </div>
</template>

<script>
import Article from "@jx3box/jx3box-editor/src/Article.vue";
import WikiPanel from "@jx3box/jx3box-common-ui/src/wiki/WikiPanel";
import WikiRevisions from "@jx3box/jx3box-common-ui/src/wiki/WikiRevisions";
import WikiComments from "@jx3box/jx3box-common-ui/src/wiki/WikiComments";
import AchievementSingle from "@/components/cj/AchievementSingle.vue";
import Relations from "@/components/cj/Relations.vue";
import { postStat } from "@jx3box/jx3box-common/js/stat";
import { wiki } from "@jx3box/jx3box-common/js/wiki";
import { publishLink } from "@jx3box/jx3box-common/js/utils";
import { ts2str } from "@jx3box/jx3box-common/js/utils.js";
export default {
    name: "Detail",
    data() {
        return {
            wiki_post: '',
            compatible: false,
            is_empty: true,
        };
    },
    computed: {
        id() {
            return this.$route.params.source_id;
        },
        post_id: function() {
            return this.$route.params.post_id;
        },
        isRevision: function() {
            return !!this.$route.params.post_id;
        },
        author_id: function() {
            return ~~this.wiki_post.post.user_id;
        },
        client: function() {
            return this.$store.state.client;
        },
        user_name: function() {
            return this.wiki_post?.post?.user_nickname;
        },
        updated_at: function() {
            return ts2str(this.wiki_post?.post?.updated);
        },
        authors: function (){
            if (!this.isRevision) {
                return this.wiki_post?.users?.filter(user => user.id)?.map(user => {
                    return {
                        user_id: user.id,
                        user_avatar: user.avatar,
                        display_name: user.nickname
                    }
                }) || []
            }
            return []
        },
        favTitle : function (){
            return this.wiki_post?.source?.Name
        }
    },
    methods: {
        publish_url: publishLink,
        triggerStat: function() {
            if (this.client == "origin") {
                postStat("origin_cj", this.id);
            } else {
                postStat("cj", this.id);
            }
        },
        loadData: function() {
            // è·å–æœ€æ–°æ”»ç•¥
            if (this.id) {
                wiki.mix({ type: 'achievement', id: this.id, client: this.client } , { supply: 1 }).then(res => {
                    const { post, source, compatible, isEmpty, users } = res;
                    this.wiki_post = {
                        post: post,
                        source: source,
                        users
                    }
                    this.is_empty = isEmpty;
                    this.compatible = compatible;
                });
            }
            this.triggerStat();
        },
        loadRevision: function() {
            // è·å–æŒ‡å®šæ”»ç•¥
            wiki.getById(this.post_id, { type: "achievement" }).then((res) => {
                this.wiki_post = res?.data?.data;
            });
            this.triggerStat();
        },
        ts2str,
    },
    components: {
        AchievementSingle,
        WikiPanel,
        WikiRevisions,
        WikiComments,
        Relations,
        Article,
    },
    watch: {
        id: {
            handler() {
                this.loadData();
            },
        },
        post_id: {
            handler() {
                this.loadRevision();
            },
        },
    },
    mounted: function() {
        if (this.post_id) {
            this.loadRevision();
        } else {
            this.loadData();
        }
    },
};
</script>

